import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import 'bootstrap/dist/css/bootstrap.min.css';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import ListGroup from 'react-bootstrap/ListGroup'
import Badge from 'react-bootstrap/Badge'
export default class Porfolio extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <Container>
        <Row>
          <Col sm={3}>
            <Image src="../images/shiva.jpg" roundedCircle />
            <h3 class="text-center">{resumeData.name}</h3>
            <h6 class="text-center">{resumeData.mailId}</h6>
            <h6 class="text-center">{resumeData.mobile}</h6>
            <hr/>
            <ListGroup>
              <ListGroup.Item><Badge variant="secondary">Role</Badge>Full Stack Engineer</ListGroup.Item>
              <ListGroup.Item><Badge variant="secondary">Designation</Badge>Senior Associate</ListGroup.Item>
              <ListGroup.Item><Badge variant="secondary">Current Employer</Badge>Cognizant</ListGroup.Item>
            </ListGroup>
          </Col>
          <Col sm={6}>
            <h2 class="text-center">Work Experience</h2>
            <hr/>
            {
            resumeData.work_experiance && resumeData.work_experiance.map((item)=>{
              return(
                <div>
                  <div class="col-exp">
                    <Image src={item.imgurl} rounded />
                    <div class="col-exp-emp">
                      <h4>{item.Role}</h4>
                      <h5>{item.Company}</h5>
                      <div>{item.Exptime}</div>
                      <div>{item.Location}</div>
                    </div>
                  </div>
                  <hr/>
                </div>
                )
              })
            }
          </Col>
          <Col sm={3}>
            <h2 class="text-center">Skill</h2>
            <hr/>
            <ul class="portfolio-skill">
              {
              resumeData.skills && resumeData.skills.map((skill)=>{
                return(
                  <li><span>#</span> {skill.skillname}</li>
                  )
                })
              }
            </ul>
          </Col>
        </Row>
      </Container>
              
    );
  }
}